
.swiper-pagination-bullet{display:inline-block;*display:inline;*zoom:1
}
#discoverAppBar .discoverAppBar__search{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center
}
