
.swiper-pagination-bullet{display:inline-block;*display:inline;*zoom:1
}
body{background-color:#f6f6f6 !important
}
.swiper-pagination-bullet{width:8px !important;height:8px !important
}
.swiper-pagination-bullet{border-radius:100% !important;background-color:#000;opacity:.2 !important;margin:0 5px !important;cursor:pointer !important
}
.swiper-pagination-bullet-active{opacity:1 !important;background-color:#ff5e1b
}
.auto__AppBar-search-field{color:#FFF;margin-bottom:0
}
.auto__AppBar-search-field.focus-state{color:#FFF
}
.auto__AppBar-search-field .mu-text-field-hint{color:fade(#fff, 54%)
}
.auto__AppBar-search-field .mu-text-field-input{color:#171818
}
.auto__AppBar-search-field .mu-text-field-focus-line{background-color:#FFF
}
.auto__AppBar{position:fixed;top:0;background-color:rgba(255,255,255,0.1);opacity:1
}
.auto__AppBar .mu-icon{color:#ff5e1b !important
}
.auto__AppBar .mu-appbar-title{width:0 !important
}
.auto__AppBar .left{position:absolute;left:0;width:14%
}
.auto__AppBar .right{width:84%;margin-left:-3%
}
.auto__AppBar .right .mu-text-field{position:absolute;left:15%;padding-left:38% !important;margin-bottom:0 !important
}
.auto__AppBar .right .mu-text-field>.mu-text-field-icon{left:44.5% !important;top:25% !important
}
.auto__AppBar .right .mu-text-field>div>div .mu-text-field-line,.auto__AppBar .right .mu-text-field>div>div .mu-text-field-focus-line{left:0 !important
}
.auto__AppBar .right .mu-icon-button{position:absolute;right:0
}
.auto__AppBar .right .mu-text-field.has-icon{padding-left:0 !important
}
.auto__AppBar .right .focus-state>i.mu-icon{display:none !important
}
.auto__AppBar .mu-text-field-line{background-color:#9c9d9f
}
.auto__AppBar .mu-text-field-focus-line{background-color:#ff5e1b
}



